.Education-main {
    align-items: center;
    align-content: center;
    text-align: center;
    /* background-color:cadetblue; */
    background-color:rgb(35, 35, 35);
    height: min-content;
    padding: 10px;
}

.Education-content {
    /* background-color: blue; */
    background-color: transparent;
    position: relative;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    height: auto;
    width: auto;
    padding: 50px;
}

@media (max-width: 800px) {
    .Education-content {
        flex-direction: column;
    }
}

.Education-container {
    /* background-color: blueviolet; */
    background-color: transparent;
    position: relative;
    display: inline-block;
    height: max-content;
    width: 40vw;
    max-width: 40vw;
    flex-grow: 1;
    margin: auto;
    margin-top: 25px;
    margin-bottom: 25px;
}

@media (max-width: 800px) {
    .Education-container {
        width: 70vw;
        max-width: 70vw;
    }
}
.Education-logo {
    position: relative;
    background-color: grey;
    height: 100px;
    width: 100px;
    border-radius: 50%;
    left: calc(50% - 50px - 4px);
    top: 0px;
    z-index: 1;
    border: 4px solid
}
.Education-box {
    position: relative;
    background-color: aqua;
    border-radius: 35px 5px;
    box-shadow: 10px 5px rgb(50, 50, 50);
    background-image: linear-gradient(to right, rgba(255,0,0,0), rgba(0,0,255,1));
    display: block;
    text-align: center;
    padding: 10px;
    padding-top: 50px;
    top: -50px;
}