.Header {
    position: fixed;
    height: 50px;
    width: 100vw;
    /* background-color: brown; */
    background-color: black;
    text-align: center;
    color: white;
    z-index: 2;
}

.Header .Header-menu {
    display: flex;
    position: absolute;
    /* background-color: cadetblue; */
    background-color: grey;
    border-radius: 5px;
    color: white;
    width: 50px;
    height: 50px;
    top: 0%;
    right: 20px;
    justify-content: center;
    align-items: center;
}

.Header-menu.active {
    background-color: white;
    color: black;
}

.Header-drawer {
    position: absolute;
    background-color:grey;
    height: 100vh;
    top: 50px;
    width: 0px;
    right: 0%;
    overflow: hidden;
    transition: 0.5s;
    padding-left: 0px;
}

.Header-drawer.active {
    width: 200px;
}

.Header-drawer-contents {
    position: absolute;
    /* background-color:chartreuse; */
    background-color: transparent;
    width: 200px;
    height: 100vh;
    list-style-type: none;
    padding-left: 0px;
    padding-right: 0px;
}

.Header-drawer-contents li {
    position: relative;
    /* list-style: none; */
    width: 200px;
    
}

.Header-drawer-contents li:hover{
    background-color: rgb(40, 40, 40);
    width: 200px;
}

.Header-drawer-contents li a {
    position: relative;
    width: 100%;
    height: 10vh;
    display: block;
    text-decoration: none;
    color: white;
    font-weight: 500;
    text-align: center;
    padding-top: 10px
}
